@tailwind base;
@tailwind components;
@tailwind utilities;
:root {
  --transition-base: .3s cubic-bezier(.25, .1, .25, 1);
  --primary-gradient: linear-gradient(90deg, rgba(57, 170, 207, 1) 5%, rgba(30, 202, 89, 1) 100%);
}

@layer components {}
/* Fix for Fancybox */
/* Plugins */
/* fade image in after load */
.lazyload,
.lazyloading {
  opacity: 0;
  transition: opacity var(--transition-base);
}

.lazyloaded {
  opacity: 1;
  transition: opacity var(--transition-base);
}

.container-fluid {
  padding-right: 8px;
  padding-left: 8px;
}
@media only screen and (max-width: theme("screens.mdMax.max")) {
  .container {
    padding-right: 40px;
    padding-left: 40px;
  }
}

