h1,
.h1 {
  font-size: 32px;
  line-height: 1.2;
  font-weight: 700;
}
@media only screen and (min-width: theme("screens.md")) {
  h1,
  .h1 {
    font-size: 106px;
    line-height: 1;
    letter-spacing: -7px;
  }
}

h2,
.h2 {
  font-size: 28px;
  line-height: 32px;
  font-weight: 700;
}
@media only screen and (min-width: theme("screens.md")) {
  h2,
  .h2 {
    font-size: 56px;
    line-height: 1;
  }
}

h3,
.h3 {
  font-size: 24px;
  line-height: 26px;
  font-weight: 700;
}
@media only screen and (min-width: theme("screens.md")) {
  h3,
  .h3 {
    font-size: 32px;
    line-height: 1;
  }
}

h4,
.h4 {
  font-size: 16px;
  line-height: 33px;
  font-weight: 500;
}
@media only screen and (min-width: theme("screens.md")) {
  h4,
  .h4 {
    font-size: 24px;
    line-height: 33px;
  }
}

h5,
.h5 {
  font-size: 18px;
  line-height: 30px;
  font-weight: 500;
}
p,
.p {
  font-size: 18px;
  line-height: 30px;
  font-weight: 500;
}
b,
strong {
  font-weight: 700;
}

a {
  font-size: 18px;
  line-height: 30px;
  font-weight: 500;
  text-decoration: underline;
}
a:hover {
  text-decoration: none;
}

input:focus {
  outline: none;
}

textarea:focus {
  outline: none;
}

button {
  padding: 0;
  border: 0;
  cursor: pointer;
}
button:focus {
  outline: none;
}

input[type=submit] {
  border: 0;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

div,
a,
a:active,
a:focus {
  outline: none;
}

html {
  height: 100%;
  min-height: 100%;
  @apply font-sans;
}

html.lenis,
html.lenis body {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto !important;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

.lenis.lenis-smooth iframe {
  pointer-events: none;
}

body {
  display: flex;
  height: 100%;
  min-height: 100%;
  flex-direction: column;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  color: theme("colors.primary");
  background: theme("colors.white");
  -webkit-font-smoothing: antialiased;
  @apply font-sans;
}

/* Blocks */
.main {
  flex: 1;
}
.section {
  padding: 32px 0;
}
@media only screen and (min-width: theme("screens.md")) {
  .section {
    padding: 90px 0;
  }
}

.parallax-box {
  position: relative;
  margin: 0 auto;
  padding-top: 72%;
  overflow: hidden;
  max-width: 1320px;
  background: theme("colors.blue.DEFAULT");
  border-radius: 30px;
}
@media only screen and (min-width: 1600px) {
  .parallax-box {
    padding-top: 0;
    min-height: 1111px;
  }
}
@media only screen and (max-width: theme("screens.smMax.max")) {
  .parallax-box {
    padding-top: 0;
    min-height: 516px;
  }
}
.parallax-box img {
  pointer-events: none;
}
.parallax-box__text {
  position: absolute;
  z-index: 10;
  top: 15%;
  left: 50%;
  margin: 0 auto;
  padding: 0 22px;
  width: 100%;
  max-width: 1001px;
  font-size: 6.8vw;
  line-height: 1;
  font-weight: 700;
  text-align: center;
  letter-spacing: -0.5vw;
  color: theme("colors.white");
  transform: translate(-50%, 0);
}
@media only screen and (min-width: 1600px) {
  .parallax-box__text {
    font-size: 106px;
    line-height: 1;
    letter-spacing: -8px;
  }
}
@media only screen and (max-width: theme("screens.smMax.max")) {
  .parallax-box__text {
    position: relative;
    top: 0;
    left: 0;
    padding-top: 70px;
    font-size: 32px;
    line-height: 1.4;
    letter-spacing: -2px;
    transform: translate(0, 0);
  }
}
.parallax-box__text img {
  width: 20vw;
}
@media only screen and (min-width: 1600px) {
  .parallax-box__text img {
    width: 316px;
  }
}
@media only screen and (max-width: theme("screens.smMax.max")) {
  .parallax-box__text img {
    width: 102px;
    min-width: 102px;
  }
}
.parallax-box__bg {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
}
.parallax-box__bg img {
  width: 100%;
  height: auto;
}
.parallax-box__gr2 {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
}
.parallax-box__gr2 img {
  width: 100%;
  height: auto;
}
.parallax-box__mountain2 {
  position: absolute;
  z-index: 2;
  bottom: -5%;
  left: 0;
  width: 100%;
}
.parallax-box__mountain2 img {
  width: 100%;
  height: auto;
}
.parallax-box__mountain1 {
  position: absolute;
  z-index: 3;
  bottom: 0;
  left: 0;
  width: 100%;
}
.parallax-box__mountain1 img {
  width: 100%;
  height: auto;
}
.parallax-box__gr {
  position: absolute;
  z-index: 4;
  bottom: 0;
  left: 0;
}
.parallax-box__gr img {
  width: 100%;
  height: auto;
}
.parallax-box__cloud {
  position: absolute;
  z-index: 5;
  right: 0;
  bottom: 10%;
  width: 70%;
}
@media only screen and (max-width: theme("screens.sm")) {
  .parallax-box__cloud {
    bottom: 22%;
  }
}
.parallax-box__cloud img {
  width: 100%;
  height: auto;
}

.gradient-link {
  text-decoration: none;
  color: theme("colors.primary");
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}